import Bmob from "hydrogen-js-sdk"
export default{
	data(){
		return {
			nickname:'',
			newNickname:'',
			objectId:''
		}
	},
	onLoad: function (option) {
		this.nickname = option.nickname;
		this.objectId = option.objectId;
	},
	methods:{
		modifyNickname(){
			var THAT = this;
			if(!THAT.newNickname){
				uni.showToast({
					title:'请输入新昵称'
				})
			}else{
				const query = Bmob.Query('_User');
				query.get(this.objectId).then(res => {
				  res.set('nickname',THAT.newNickname);
				  res.save();
				  uni.showModal({
				  	title:'提示',
					content:'修改成功',
					showCancel:false,
					success: (res) => {
						if(res.confirm==true){
							uni.navigateBack({
								delta:1
							})
						}
					}
				  })
				}).catch(err => {
				  console.log(err)
				})
			}
		}
	}
}
